
.brand-index {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 530px;
  min-height: 360px;
}
.brand-index .operation {
    width: 530px;
}
.brand-index .operation .row-item {
      display: flex;
}
.brand-index .operation .item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 230px;
      height: 150px;
      margin: 15px;
      background-color: #98B42C;
      border-radius: 10px;
}
.brand-index .operation .item .label {
        margin-bottom: 20px;
        text-align: center;
}
.brand-index .operation .item .img {
        height: 40px;
        width: 40px;
        opacity: 0.75;
}



