
.statistics-dealer {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.statistics-dealer .header {
    width: 100%;
}
.statistics-dealer .data-holder .data-header {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.statistics-dealer .data-holder .data-header > div {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      width: 170px;
      height: 66px;
      border-radius: 10px;
      background: #98B42C;
}
.statistics-dealer .data-holder .data-header > div > img {
        width: 50px;
}
.statistics-dealer .data-holder .data-header > div .amount {
        margin-left: 10px;
        text-align: center;
}
.statistics-dealer .data-holder .canvas-holder {
    /*margin-top: 20px;*/
    width: 100%;
    height: 350px;
    /*background: white;*/
}
.statistics-dealer .table-wrap {
    flex: 1;
    margin-bottom: 60px;
}
.statistics-dealer .table-wrap .table-content {
      padding-bottom: 0;
}
.statistics-dealer .table-wrap .no-data-content {
      bottom: 0;
      height: 100%;
      position: relative;
}



