
.component-list {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.component-list .header {
    height: auto;
}
.component-list .header .el-form-item {
      margin-bottom: 0;
}
.component-list .el-form-item__content {
    width: 200px;
}
.component-list .loading {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.component-list .table-wrap {
    flex: 1;
}
.component-list .input-sn:after {
    top: 8px;
}
.component-list .no-data-content {
    position: relative;
}
.component-list .table-content {
    padding-bottom: 0;
}



