
.assembler-order-list {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.assembler-order-list .table-wrap {
    flex: 1;
}
.assembler-order-list .list-form-inline .el-form-item {
    margin-right: 2px !important;
    margin-bottom: 0;
}
.assembler-order-list .list-form-inline .el-form-item__content {
    width: 180px !important;
}
.assembler-order-list .header {
    height: auto;
}
.assembler-order-list .loading {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.assembler-order-list .long-td {
    max-width: 300px;
    text-overflow: ellipsis;
    overflow: hidden;
}
.assembler-order-list .long-td div {
    max-width: 300px;
    text-overflow: ellipsis;
    overflow: hidden;
}



